}

.justify-center {
    justify-content: center;
}

.container {
    max-width: 1140px;
    margin: auto;
    width: calc(100% - 48px);
}

.section-titolo {
    color: var(--nero);
    text-transform: uppercase;
    letter-spacing: 8px;
    border-bottom: 2px solid var(--nero);
    text-shadow: 3px 3px 8px #555;
}

/* root */
:root {
    --arancio: #D95204;
    --rosso: #F20505;
    --rosso-scuro: #8C0303;
    --nero: #0D0D0D;
}

/* !section-1 home*/    
.header {
    width: 100%;
    height: 100svh;
    display: inline-flex;
    justify-content: center;
    align-items: center;
     & .wrap-grid{
        display: grid;
        /* gap: 20%; */
        grid-template-columns: 1fr 1fr;
        grid-template-rows: 1fr ;
        
     }
}
.img-persona{
    width: 355px;
    height: 355px;
    border-radius: 70px;

}


/* !fine section-1 */



/* !section-2 */
.section-2 {
    width: 100%;
    height: 100%;
    margin: 100px 0;
    display: flex;
    justify-content: center;
}

.section-2-progetti{
    display: grid;    
    grid-template-columns: repeat(2, 1fr);
    /* grid-template-rows: 1fr ; */
    gap: 50px;
}

.stile-progetti {
    width: 100%;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

.progetto {
    margin: 10px;
    background-color: #fff;
    box-shadow: 0.2rem 0.2rem 0.5rem #555;
    position: relative;
    overflow: hidden;
}

.testo-progetto {
    text-align: center;
    letter-spacing: 5px;
    position: absolute;
    top: -100%;
    transition: top 0.3s;
    z-index: 10;
}

.progetto:hover .testo-progetto {
    top: 30px;
    transition: top 0.3s 0.3s;
}

.titolo-progetto {
    font-size: 44px;
    font-weight: 900;
    color: var(--nero);
    text-transform: capitalize;
    color: white;
    text-shadow: 1px 1px 2px black, 0 0 1em black, 0 0 0.2em black;
}

.sottotitolo-progetto {
    font-size: 22px;
    color: white;
    text-shadow: 1px 1px 2px black;

}

.img-progetto {
    width: auto;
    transition: opacity 0.3s;
    transition: blur 0.3s;
    
}

.progetto:hover .img-progetto {
    opacity: 0.8;
    filter: blur(0.2rem);
}

.link-progetto {
    font-size: 28px;
    font-weight: 700;
    letter-spacing: 2px;
    color: white;
    text-shadow: 1px 1px 2px black;
    background: var(--rosso-scuro);
    border-radius: 70px;
    padding: 14px 28px;
    position: absolute;
    bottom: -100%;
    transition: bottom 0.3s;

}

.progetto:hover .link-progetto {
    bottom: 60px;
    transition: bottom 0.3s 0.3s;
}

.link-progetto:hover {
    background: var(--rosso);
    transform: scale(1.2);
    transition: all .52s cubic-bezier(.215, .61, .355, 1);
}

/* !fine section-2 */

/* !section-3 */
.section-3 {
    width: 100%;
    height: 100vh;


}



/* !fine section-3 */

/* !section-4 */
.section-4 {
    width: 100%;
    height: 70vh;
    background-color: #555;
}

.titolo-section-4 {
    color: white;
    box-shadow: none;
    border-color: white;

}

.form {
    width: 25%;
}

.form input,
.form textarea {
    width: 100%;
    padding: 10px;
    margin: 5px 0;
    border-radius: 18px;
    border: none;

}

.form textarea {
    max-width: 100%;
    height: 70px;
    max-height: 140px;
}
.btn{
    font-size: 18px;
    font-weight: 700;
    letter-spacing: 2px;
    color: white;
    text-shadow: 1px 1px 2px black;
    background: var(--rosso-scuro);
    border-radius: 70px;
    padding: 10px 20px;


}

/* !fine section-4 */
/* ! section-5 footer */
.section-5 {
    width: 100%;
    height: 30vh;
    background-color: #222;
}

.social-media {
    margin-bottom: 30px;
}

.social-media-link {
    margin: 0 5px;
}

.social-media-link i {
    font-size: 50px;
    transition: all .250s;
}

.copyright {
    color: white;
    letter-spacing: 3px;
    text-align: center;
}

/* !fine section-5 */






